html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

html {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  overflow-y: scroll;
  width: 100%;
  height: 100%;
  min-height: 100%;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  width: 100%;
  min-height: 100%;
}

iframe {
  border: 0;
}

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}

a,
area,
button,
[role=button],
input,
label,
select,
summary,
textarea,
[tabindex] {
  touch-action: manipulation;
}

summary {
  display: list-item;
}

::-moz-selection {
  background: var(--blue-200);
  text-shadow: none;
}

::selection {
  background: var(--blue-200);
  text-shadow: none;
  color: #ffffff;
}

svg {
  fill: currentcolor;
}

[aria-busy=true] {
  cursor: progress;
}

[aria-controls] {
  cursor: pointer;
}

[aria-disabled] {
  cursor: default;
}

[hidden][aria-hidden=false] {
  position: absolute;
  display: inherit;
  clip: rect(0 0 0 0);
}
[hidden][aria-hidden=false]:focus {
  clip: auto;
}

/* stylelint-disable at-rule-no-vendor-prefix */
@-ms-viewport {
  width: device-width;
}
/* stylelint-enable at-rule-no-vendor-prefix */
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 24px;
}

p {
  margin: 0 0 24px;
}

u,
ins,
abbr[title] {
  text-decoration: none;
}

abbr[title] {
  border-bottom: 1px dotted var(--gray-700);
}

ins {
